@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

nav {
  width: 100%;
  padding: 20px 0;
  margin: 0 auto;
  background: #f5f5f2;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  z-index: 100000;
}

nav .box-logo {
  color: #f8d660;
  font-family: 'Pacifico';
  line-height: 1;
  font-weight: 400;
  font-size: 32px;
  display: flex;
  flex-direction: row;
}

nav .box-text {
  font-weight: 400;
  font-size: 12px;
  font-style: italic;
  color: #555;
  padding-bottom: 20px;
}

nav .box-actions {
  margin-top: auto;
  display: flex;
  flex-direction: row;
  color: #555;
}

nav .box-actions * {
  font-size: 14px;
  margin-left: 10px;
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}
