:root{--color-common-background: #fff}*{margin:0;padding:0;outline:0;box-sizing:border-box}html,body,#root{height:100%}html{background:#f8d660;-webkit-font-smoothing:antialiased !important}body::-webkit-scrollbar{width:8px;height:10px;background:#f5f5f2}body::-webkit-scrollbar-thumb{border-radius:8px;background:#666}::selection{background:#fdf}::-moz-selection{background:#fdf}::-moz-focus-inner{border:0}body,input,button{font-family:"Lato",sans-serif}a{text-decoration:none;color:inherit}.container{width:100%;margin:0 auto;border-radius:8px;align-items:center;display:flex;flex-direction:column}.content{width:calc(100% - 240px)}
.card{box-shadow:0 3px 6px rgba(0,0,0,.16),0 3px 6px rgba(0,0,0,.23);width:100%;height:600px;height:650px;margin-bottom:60px;border-radius:8px;background:#f3f3f0;display:flex;flex-direction:column}.card .card-header .title h3{cursor:pointer;color:#000}.card .card-header .seller span,.card .card-info .hashtags{cursor:pointer}.card .card-info .description p,.card .card-header .seller span{color:#222}.card .card-info .description{margin-bottom:2px}.card .card-img{cursor:pointer;width:100%;height:480px;overflow:hidden}.card .card-img img{width:100%;height:100%;object-fit:cover}.card .card-header,.card .card-info{padding:1rem}
